body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;

}

.button{
  font-size: 30px;
  background-color: #d1d1d1;
  width: 80px;
  height: 70px;
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.button:hover{
  cursor: pointer;
}

#calculator{
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #555555;
  width: 400px;
  margin: auto;
  margin-top: 20px;
}

#display {
  text-align: right;
  font-size: 30px;
  background-color: #d2d2d2;
  width: 330px;
  height: 40px;
  margin:auto;
  padding-right: 20px;
  padding-bottom: 10px;

}

#expression {
  text-align: right;
  font-size: 15px;
  background-color: #d2d2d2;
  width: 330px;
  height: 20px;
  margin:auto;
  padding-right: 20px;
}

#container{
  display: grid;
  grid-template-columns: 80px 80px 80px 80px;
  grid-gap: 10px;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

